body {

}
header {
    padding-left: 20%;
    padding-right: 20%;
}
header li {
    padding-top: 5px;
    font-weight: bold;
}

main {
    padding-left: 20%;
    padding-right: 20%;
}

.btn {
    display: none;
}
li {
    padding-right: 30px;
    padding-left: 10px;
    margin-top: 15px;
    display: inline;
}

.nav ul{
    text-align: center;
    padding-left: unset;
}

header li a {
    padding: 20px;
}
li a:hover {
    background-color: rgb(128, 128, 128);
}

li:hover > ul {
    display: block!important;
}

.nav ul ul {
    display: none;
    position: absolute;
    background-color: rgb(220, 220, 220);
    padding-left: 20px;
    margin-top: 10px;
    padding-bottom: 10px;
}

.nav ul ul li {
    margin-top: 10px;
    display: block;
    text-align: center;
}
footer {
    background-color: black;
    color: white;
}
.grid-container {
    display: inline-grid;
    grid-template-columns: auto auto;
    padding: 25px;
    padding-left: 35%;
    padding-right: 35%;
  }

.grid-item {
    border: 1px solid rgba(0, 0, 0, 0.8);
    padding: 20px;
    font-size: 10px;
    text-align: center;
}

footer li {
    margin-top: 0px;
    padding-bottom: 20px;
    display: list-item;
    list-style-type: none;
}

footer a {
    text-decoration: none;
    color: white;
}

@media all and (max-width: 768px) {
    header{
        padding: unset;
    }
    main {
        padding: unset;
        padding: 10px;
    }
    .btn {
        display: block;
    }

    .container .btn {
        position: absolute;
        top: 5%;
        left: 10%;
        transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
        background-color: #555;
        color: white;
        font-size: 16px;
        border: none;
        cursor: pointer;
        border-radius: 5px;
      }
    .grid-container {
        padding: unset;
    }
    
    footer {
        text-align: center;
    }
    .nav ul {
        padding-left: unset;
    }
    .nav ul li {
        padding: 0px ;
    }
    .submenu {
        display: list-item;
    }
}