body {
}

header {
  padding-left: 20%;
  padding-right: 20%;
}

header li {
  padding-top: 5px;
  font-weight: bold;
}

main {
  padding-left: 20%;
  padding-right: 20%;
}

.btn {
  display: none;
}

li {
  margin-top: 15px;
  padding-left: 10px;
  padding-right: 30px;
  display: inline;
}

.nav ul {
  text-align: center;
  padding-left: unset;
}

header li a {
  padding: 20px;
}

li a:hover {
  background-color: gray;
}

li:hover > ul {
  display: block !important;
}

.nav ul ul {
  background-color: #dcdcdc;
  margin-top: 10px;
  padding-bottom: 10px;
  padding-left: 20px;
  display: none;
  position: absolute;
}

.nav ul ul li {
  text-align: center;
  margin-top: 10px;
  display: block;
}

footer {
  color: #fff;
  background-color: #000;
}

.grid-container {
  grid-template-columns: auto auto;
  padding: 25px 35%;
  display: inline-grid;
}

.grid-item {
  text-align: center;
  border: 1px solid rgba(0, 0, 0, .8);
  padding: 20px;
  font-size: 10px;
}

footer li {
  margin-top: 0;
  padding-bottom: 20px;
  list-style-type: none;
  display: list-item;
}

footer a {
  color: #fff;
  text-decoration: none;
}

@media (max-width: 768px) {
  header {
    padding: unset;
  }

  main {
    padding: unset;
    padding: 10px;
  }

  .btn {
    display: block;
  }

  .container .btn {
    color: #fff;
    cursor: pointer;
    background-color: #555;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    position: absolute;
    top: 5%;
    left: 10%;
    transform: translate(-50%, -50%);
  }

  .grid-container {
    padding: unset;
  }

  footer {
    text-align: center;
  }

  .nav ul {
    padding-left: unset;
  }

  .nav ul li {
    padding: 0;
  }

  .submenu {
    display: list-item;
  }
}

